import { createSlice } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';

export interface IStatusState {
    isLoading: boolean;
    isImageView: boolean;
    imageViewIndex: number | null;
    isGalleryOpen: boolean;
    isAutoScrollEnabled: boolean;
}

const initialState: IStatusState = {
    isLoading: true,
    isImageView: false,
    imageViewIndex: null,
    isGalleryOpen: false,
    isAutoScrollEnabled: false,
};

export const statusSlice = createSlice({
    name: 'status',
    initialState,
    reducers: {
        startLoading: state => {
            state.isLoading = true;
        },
        completeLoading: state => {
            state.isLoading = false;
        },
        openImageView: (state, action: PayloadAction<number | null>) => {
            state.isImageView = true;
            state.imageViewIndex = action.payload;
        },
        closeImageView: state => {
            state.isImageView = false;
            state.imageViewIndex = null;
        },
        toggleGalleryOpen: state => {
            state.isGalleryOpen = !state.isGalleryOpen;
        },
        enableAutoScrole: state => {
            state.isAutoScrollEnabled = true;
        },
        disableAutoScrole: state => {
            state.isAutoScrollEnabled = false;
        },
        toggleAutoScrole: state => {
            state.isAutoScrollEnabled = !state.isAutoScrollEnabled;
        },
    },
});

// Action creators are generated for each case reducer function
export const {
    startLoading,
    completeLoading,
    openImageView,
    closeImageView,
    toggleGalleryOpen,
    enableAutoScrole,
    disableAutoScrole,
    toggleAutoScrole,
} = statusSlice.actions;

export const statusReducer = statusSlice.reducer;
