import { createSlice } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';

export interface IUser {
    id: number;
    name: string;
    age: string;
    lastOnline: number;
    status: string;
    shortStatus: string;
    tags: ITag[];
    isSaved?: boolean;
    isReady: boolean;
    note?: string;
}

interface ITag {
    id: number;
    tag: string;
}

export interface IUserState {
    user: IUser;
    savedUsers: IUser[];
}

const initialState: IUserState = {
    user: {
        id: 0,
        name: '',
        age: '',
        lastOnline: 0,
        status: '',
        shortStatus: '',
        tags: [],
        isReady: false,
    },
    savedUsers: JSON.parse(localStorage.getItem('savedProfiles') ?? '[]'),
};

export const userSlice = createSlice({
    name: 'user',
    initialState,
    reducers: {
        setUser: (state, action: PayloadAction<IUser>) => {
            state.user = action.payload;

            const isSavedUser = state.savedUsers.some(
                user => user.id === state.user.id
            );
            state.user.isSaved = isSavedUser;
        },
        addSavedUser: (state, action: PayloadAction<IUser>) => {
            state.savedUsers.unshift(action.payload);
            state.user.isSaved = true;
            localStorage.setItem(
                'savedProfiles',
                JSON.stringify(state.savedUsers)
            );
        },
        addNote: (
            state,
            action: PayloadAction<{ idx: number; note: string }>
        ) => {
            const { idx, note } = action.payload;
            state.savedUsers[idx].note = note;
            localStorage.setItem(
                'savedProfiles',
                JSON.stringify(state.savedUsers)
            );
        },
        removeNote: (state, action: PayloadAction<number>) => {
            const idx = action.payload;
            state.savedUsers[idx].note = undefined;
            localStorage.setItem(
                'savedProfiles',
                JSON.stringify(state.savedUsers)
            );
        },
        removeSavedUser: (state, action: PayloadAction<number>) => {
            state.savedUsers = state.savedUsers.filter(
                user => user.id !== action.payload
            );
            state.user.isSaved = false;
            localStorage.setItem(
                'savedProfiles',
                JSON.stringify(state.savedUsers)
            );
        },
        getUser: (_, action: PayloadAction<string>) => {},
        resetUser: state => {
            state.user = initialState.user;
        },
    },
});

// Action creators are generated for each case reducer function
export const {
    setUser,
    resetUser,
    addSavedUser,
    getUser,
    removeSavedUser,
    addNote,
    removeNote,
} = userSlice.actions;

export const userReducer = userSlice.reducer;
