import { ICHat } from '../../common/models/index';
import { createSlice } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';

export interface IChatListState {
    chatList: ICHat[];
    activeIndex: number;
    chatsPage: number;
    haveMoreChats: boolean;
}

const initialState: IChatListState = {
    activeIndex: 0,
    chatList: [],
    chatsPage: 1,
    haveMoreChats: false,
};

export const chatListSlice = createSlice({
    name: 'chatList',
    initialState,
    reducers: {
        setChatList: (state, action: PayloadAction<ICHat[]>) => {
            state.chatList = action.payload;
            state.haveMoreChats = true;
            state.chatsPage = 1;
            if (action.payload.length < 15) state.haveMoreChats = false;
        },
        addChatList: (state, action: PayloadAction<ICHat[]>) => {
            state.chatList.push(...action.payload);
            if (action.payload.length < 15) state.haveMoreChats = false;
        },
        setActiveIndex: (state, action: PayloadAction<number>) => {
            const idx = action.payload;
            state.activeIndex = idx;
            state.chatList[idx].isVisited = true;
        },
        getChatList: (_, action: PayloadAction<string>) => {},
        updateChatList: (_, action: PayloadAction<string>) => {},
        getMoreChatList: (state, action: PayloadAction<string>) => {
            state.chatsPage++;
        },
        setHaveMoreChats: (state, action: PayloadAction<boolean>) => {
            state.haveMoreChats = action.payload;
        },
    },
});

// Action creators are generated for each case reducer function
export const {
    setChatList,
    addChatList,
    setActiveIndex,
    getChatList,
    updateChatList,
    getMoreChatList,
    setHaveMoreChats,
} = chatListSlice.actions;

export const chatListReducer = chatListSlice.reducer;
