import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { RootState } from '../app/store';
import { setActiveIndex } from '../features/chatList/chatListSlice';
import {
    toggleAutoScrole,
    toggleGalleryOpen,
} from '../features/status/statusSlice';
import { UserProfile } from './UserProfile';

interface ChatControlsProps {
    userId: string;
    interlocutorId: string;
}

export const ChatControls = ({ userId, interlocutorId }: ChatControlsProps) => {
    const dispatch = useDispatch();
    const { activeIndex, chatList } = useSelector(
        (state: RootState) => state.chatList
    );
    const { galleryCounter } = useSelector((state: RootState) => state.chat);
    const { isLoading, isGalleryOpen, isAutoScrollEnabled } = useSelector(
        (state: RootState) => state.status
    );
    const navigate = useNavigate();

    const redirectToChat = (step: number) => {
        const chatIdx = activeIndex + step;
        dispatch(setActiveIndex(chatIdx));
        navigate(`/${userId}/chat/${chatList[chatIdx].human.id}`, {
            replace: true,
        });
    };

    return (
        <div className="row justify-content-center position-relative mb-2">
            <div className="d-flex justify-content-between align-items-center mb-2 position-relative">
                <div
                    className={
                        'btn btn-outline-primary my-1' +
                        (activeIndex - 1 < 0 ? ' disabled opacity-0' : '')
                    }
                    onClick={() => {
                        redirectToChat(-1);
                    }}
                >
                    ←
                </div>
                <UserProfile userId={interlocutorId} />
                <div
                    className={
                        'btn btn-outline-primary my-1' +
                        (activeIndex + 1 > chatList.length - 1
                            ? ' disabled opacity-0'
                            : '')
                    }
                    onClick={() => {
                        redirectToChat(+1);
                    }}
                >
                    →
                </div>
            </div>
            <div className="text-center">
                <div
                    className="btn btn-secondary me-2"
                    onClick={() => {
                        navigate(-1);
                    }}
                >
                    Back
                </div>
                <div
                    className={
                        'btn btn-primary me-2' + (isLoading ? ' disabled' : '')
                    }
                    onClick={() => {
                        dispatch(toggleGalleryOpen());
                    }}
                >
                    {isGalleryOpen ? 'Close gallery' : 'Gallery'}
                    {` (${galleryCounter})`}
                </div>
                <div
                    className={
                        'btn ' +
                        (isAutoScrollEnabled
                            ? 'btn-primary'
                            : 'btn-secondary') +
                        (isLoading ? ' disabled' : '')
                    }
                    onClick={() => {
                        dispatch(toggleAutoScrole());
                    }}
                >
                    AutoScroll
                </div>
            </div>
        </div>
    );
};
