import { ICHat } from './../../common/models/index';
import { RootState } from './../store';
import {
    addChatList,
    getChatList,
    getMoreChatList,
    setChatList,
    updateChatList,
} from './../../features/chatList/chatListSlice';
import { getNewToken, setToken } from './../../features/auth/authSlice';
import axios from 'axios';
import { call, put, takeLatest, select, take } from 'redux-saga/effects';
import {
    completeLoading,
    startLoading,
} from '../../features/status/statusSlice';
import { getUser } from '../../features/user/userSlice';

interface IRequestChatList {
    userId: string;
    page?: string;
    token: string;
}

const requestChatList = ({ userId, token, page }: IRequestChatList) =>
    axios.get('https://dialog.api.dc2.a4sex.net/api/contact_intimates', {
        params: {
            userId,
            page,
        },
        headers: {
            Authorization: 'Bearer ' + token,
        },
    });

interface IWorkerArgs {
    type: string;
    payload: string;
}

const compareChats = (data: ICHat[], chatList: ICHat[]) => {
    const processedData: ICHat[] = [];
    for (const newChatData of data.filter(({ dialog }) => dialog)) {
        let isNewChat = true;
        for (const oldChatData of chatList.filter(({ dialog }) => dialog)) {
            if (newChatData.id !== oldChatData.id) continue;
            isNewChat = false;
            if (newChatData.dialog.message === oldChatData.dialog.message)
                processedData.push(oldChatData);
            else processedData.push(newChatData);
        }
        if (isNewChat) processedData.push(newChatData);
    }
    return processedData;
};

function* getChatListGenerator(userId: string): Generator<any, any, any> {
    try {
        const { token } = yield select((state: RootState) => state.auth);
        const { chatList } = yield select((state: RootState) => state.chatList);
        const { data } = yield call(requestChatList, {
            userId,
            token,
        });
        const processedData = compareChats(data, chatList);
        yield put(setChatList(processedData));
    } catch (error: any) {
        if (error.response) {
            if (error.response.status === 401) {
                yield put(getNewToken());
                yield take(setToken.type);
                yield put(getChatList(userId));
            } else console.log(error.response);
        }
    }
}

function* getChatListWorker({
    payload: userId,
}: IWorkerArgs): Generator<any, any, any> {
    yield put(startLoading());
    yield put(getUser(userId));
    yield call(getChatListGenerator, userId);
    yield put(completeLoading());
}

function* updateChatListWorker({
    payload: userId,
}: IWorkerArgs): Generator<any, any, any> {
    yield call(getChatListGenerator, userId);
}

function* getMoreChatListWorker({
    payload: userId,
}: IWorkerArgs): Generator<any, any, any> {
    const { chatsPage: page } = yield select(
        (state: RootState) => state.chatList
    );
    try {
        const { token } = yield select((state: RootState) => state.auth);
        const { data } = yield call(requestChatList, {
            userId,
            page,
            token,
        });
        yield put(addChatList(data.filter(({ dialog }: ICHat) => dialog)));
    } catch (error: any) {
        if (error.response) {
            if (error.response.status === 401) {
                yield put(getNewToken());
                yield take(setToken.type);
                yield put(getMoreChatList(userId));
            } else console.log(error.response);
        }
    }
}

export function* chatListWatcher() {
    yield takeLatest(getChatList.type, getChatListWorker);
    yield takeLatest(updateChatList.type, updateChatListWorker);
    yield takeLatest(getMoreChatList.type, getMoreChatListWorker);
}
