import { useEffect, useRef } from 'react';
import { useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import {
    clearChatMessages,
    getAllChatMessages,
    updateChatMessages,
} from '../features/chat/chatSlice';
import { Loader } from '../components/Loader';
import { IMG_DOMEN } from '../app/config';
import { ChatGallery } from '../components/ChatGallery';
import htmlParser from 'html-react-parser';
import { RootState } from '../app/store';
import { ChatControls } from '../components/ChatControls';
import { ImageView } from '../components/ImageView';
import { openImageView } from '../features/status/statusSlice';
import { resetUser } from '../features/user/userSlice';

export const ChatPage = () => {
    const { userId = '', interlocutorId = '' } = useParams();
    const dispatch = useDispatch();
    const { isLoading, isImageView, isGalleryOpen, isAutoScrollEnabled } =
        useSelector((state: RootState) => state.status);
    const { chatMessages } = useSelector((state: RootState) => state.chat);

    const chatContainer = useRef<HTMLDivElement>(null!);

    useEffect(() => {
        if (isGalleryOpen || isLoading) return;
        if (isAutoScrollEnabled) {
            chatContainer.current.scrollTop =
                chatContainer.current.scrollHeight!;
        }
    }, [isAutoScrollEnabled, chatMessages, isGalleryOpen, isLoading]);

    useEffect(() => {
        if (!chatMessages.length) return;
        const intervalId = setInterval(() => {
            dispatch(
                updateChatMessages({
                    userId,
                    interlocutorId,
                })
            );
        }, 15000);
        return () => {
            clearInterval(intervalId);
        };
    }, [dispatch, userId, interlocutorId, chatMessages.length]);

    useEffect(() => {
        dispatch(getAllChatMessages({ userId, interlocutorId }));
        return () => {
            dispatch(clearChatMessages());
            dispatch(resetUser());
        };
    }, [dispatch, interlocutorId, userId]);

    const renderDialog = () => {
        let i = 0;
        const res: JSX.Element[] = [];
        for (const message of chatMessages) {
            const k = i;
            const [date, time] = new Date(message.added)
                .toLocaleString('ru')
                .split(', ');
            res.push(
                <div
                    className={
                        'fs-5 mb-2 ' +
                        (message.fromId === +userId! ? ' text-end' : '')
                    }
                    key={message.id}
                >
                    <div
                        className="border p-2 d-inline-block"
                        style={{ maxWidth: '80%', borderRadius: '10px' }}
                    >
                        {message.isImg ? (
                            <img
                                loading="lazy"
                                src={IMG_DOMEN + message.src}
                                alt={message.message}
                                className="d-block w-100"
                                role="button"
                                onClick={() => {
                                    dispatch(openImageView(k));
                                }}
                            />
                        ) : (
                            <div>{htmlParser(message.message)}</div>
                        )}
                        <div className="text-secondary">{`${time} ${date}`}</div>
                    </div>
                </div>
            );
            if (message.isImg) i++;
        }
        return res;
    };

    const dialogMarkup = renderDialog();

    const chatControlsProps = {
        userId,
        interlocutorId: interlocutorId!,
    };

    if (isLoading)
        return (
            <>
                <ChatControls {...chatControlsProps} />
                <Loader align="center" size="6rem" className="mt-5" />
            </>
        );

    return (
        <>
            <ChatControls {...chatControlsProps} />
            {isGalleryOpen && (
                <ChatGallery
                    profileId={+userId!}
                    interlocutorId={+interlocutorId!}
                />
            )}
            {!isGalleryOpen &&
                (chatMessages.length ? (
                    <div
                        ref={chatContainer}
                        className="text-break pe-2"
                        style={{ maxHeight: '80vh', overflow: 'auto' }}
                    >
                        {dialogMarkup}
                    </div>
                ) : (
                    <h2 className="text-center">No messages</h2>
                ))}
            {isImageView && <ImageView />}
        </>
    );
};
