import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { RootState } from '../app/store';
import { useEffect } from 'react';
import {
    getMoreChatList,
    setActiveIndex,
    updateChatList,
} from '../features/chatList/chatListSlice';
import { UserProfile } from './UserProfile';
import htmlParser from 'html-react-parser';

interface ChatListProps {
    userId?: string;
}

export const ChatList = ({ userId }: ChatListProps) => {
    const dispatch = useDispatch();
    const { isLoading } = useSelector((state: RootState) => state.status);
    const { chatList, haveMoreChats } = useSelector(
        (state: RootState) => state.chatList
    );
    const navigate = useNavigate();

    useEffect(() => {
        if (!chatList.length) return;
        const intervalId = setInterval(() => {
            dispatch(updateChatList(userId!));
        }, 15000);
        return () => {
            clearInterval(intervalId);
        };
    }, [dispatch, userId, chatList.length]);

    const chatListMarkup = chatList.map((chat, idx) => (
        <div
            className={
                'border p-2 mb-2 d-flex align-items-center flex-column flex-sm-row' +
                (chat.isVisited ? ' opacity-50' : '')
            }
            key={chat.id}
            onClick={() => {
                if (userId) dispatch(setActiveIndex(idx));
                navigate(
                    userId ? `./chat/${chat.human.id}` : `/${chat.human.id}`
                );
            }}
            role="button"
        >
            <div className="flex-shrink-0 me-sm-2 ">
                <b className="me-2">{chat.human.name ?? 'Unknown'}</b>
                <span className="text-danger">
                    {chat.human.age ?? 'unknown'}
                </span>
            </div>
            <div className="flex-fill text-sm-end text-break">
                {htmlParser(chat.dialog.message)}
            </div>
        </div>
    ));
    return (
        <div className="row justify-content-center">
            <div className="col-sm-10 col-md-8 col-lg-6 mb-2">
                {userId && (
                    <UserProfile
                        userId={userId}
                        className="mb-2"
                        showTags={true}
                    />
                )}
                {!isLoading &&
                    (chatList.length ? (
                        <>
                            {chatListMarkup}
                            {userId && haveMoreChats && (
                                <>
                                    <div className="text-center">
                                        <div className="col-auto">
                                            <div
                                                className="btn btn-primary"
                                                onClick={() => {
                                                    dispatch(
                                                        getMoreChatList(userId)
                                                    );
                                                }}
                                            >
                                                Load more
                                            </div>
                                        </div>
                                    </div>
                                </>
                            )}
                        </>
                    ) : (
                        <h2 className="text-center">No chats found..</h2>
                    ))}
            </div>
        </div>
    );
};
