import { getUser } from './../../features/user/userSlice';
import { RootState } from './../store';
import { getNewToken, setToken } from '../../features/auth/authSlice';
import axios from 'axios';
import { call, put, select, take, takeLatest } from 'redux-saga/effects';
import { setUser } from '../../features/user/userSlice';

interface IRequestUserArgs {
    userId: string;
    token: string;
}

const requestUser = ({ userId, token }: IRequestUserArgs) =>
    axios.get('https://search.api.dc2.a4sex.net/api/users/' + userId, {
        headers: {
            Authorization: 'Bearer ' + token,
        },
    });

interface IWorkerArgs {
    type: string;
    payload: string;
}

function* getUserWorker({
    payload: userId,
}: IWorkerArgs): Generator<any, any, any> {
    try {
        const { token } = yield select((state: RootState) => state.auth);
        const { data } = yield call(requestUser, { userId, token });
        const lastOnline = data.last;
        let lastOnlineString = `${lastOnline} seconds`;
        if (lastOnline > 60) {
            // minutes
            if (lastOnline > 60 * 60) {
                // days
                if (lastOnline > 60 * 60 * 24) {
                    lastOnlineString = `${(lastOnline / 60 / 60 / 24).toFixed(
                        2
                    )} days`;
                } else {
                    // hours
                    lastOnlineString = `${(lastOnline / 60 / 60).toFixed(
                        2
                    )} hours`;
                }
            } else lastOnlineString = `${(lastOnline / 60).toFixed(2)} minutes`;
        }
        yield put(
            setUser({
                id: data.id,
                name: data.name ?? 'Unknown',
                age: data.age ?? 'unknown',
                lastOnline,
                status: lastOnlineString,
                shortStatus: data.last < 600 ? 'Online' : 'Offline',
                tags: data.tags ?? [],
                isReady: true,
            })
        );
    } catch (error: any) {
        if (error.response.status === 401) {
            yield put(getNewToken());
            yield take(setToken.type);
            yield put(getUser(userId));
        } else if (error.response.status === 404) {
            yield put(
                setUser({
                    id: 0,
                    name: '',
                    age: '',
                    lastOnline: 0,
                    status: '',
                    shortStatus: '',
                    tags: [],
                    isReady: true,
                })
            );
        }
    }
}

export function* userSaga() {
    yield takeLatest(getUser.type, getUserWorker);
}
