import { useDispatch, useSelector } from 'react-redux';
import { IMG_DOMEN } from '../app/config';
import { RootState } from '../app/store';
import 'swiper/css';
import 'swiper/css/pagination';
import { openImageView } from '../features/status/statusSlice';
import { clearFilter, setFilter } from '../features/chat/chatSlice';

interface ChatGalleryProps {
    profileId: number;
    interlocutorId: number;
}

export const ChatGallery = ({
    profileId,
    interlocutorId,
}: ChatGalleryProps) => {
    const dispatch = useDispatch();
    const { gallery, galleryFilter } = useSelector(
        (state: RootState) => state.chat
    );

    const filteredGallery = gallery.filter(
        image => galleryFilter === 0 || image.sender === galleryFilter
    );

    return (
        <>
            {gallery.length ? (
                <div className="text-center mb-2">
                    <div className="btn-group">
                        <div
                            className={
                                'btn btn-outline-primary' +
                                (galleryFilter === profileId ? ' active' : '')
                            }
                            onClick={() => {
                                dispatch(
                                    galleryFilter === profileId
                                        ? clearFilter()
                                        : setFilter(profileId)
                                );
                            }}
                        >
                            User
                        </div>
                        <div
                            className={
                                'btn btn-outline-primary' +
                                (galleryFilter === interlocutorId
                                    ? ' active'
                                    : '')
                            }
                            onClick={() => {
                                dispatch(
                                    galleryFilter === interlocutorId
                                        ? clearFilter()
                                        : setFilter(interlocutorId)
                                );
                            }}
                        >
                            Interlocutor
                        </div>
                    </div>
                </div>
            ) : (
                ''
            )}
            <div
                className="row"
                style={{ maxHeight: '70vh', overflow: 'auto' }}
            >
                {filteredGallery.map((image, idx) => (
                    <div
                        className="col-6 col-sm-2 col-md-3 mb-2"
                        key={image.id}
                    >
                        <div
                            className="square-image border"
                            role="button"
                            onClick={() => {
                                dispatch(openImageView(idx));
                            }}
                        >
                            <img
                                loading="lazy"
                                src={IMG_DOMEN + image.src}
                                alt={image.alt}
                            />
                        </div>
                    </div>
                ))}
                {filteredGallery.length ? (
                    ''
                ) : (
                    <h2 className="text-center">No images</h2>
                )}
            </div>
        </>
    );
};
