import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom';
import { RootState } from '../app/store';
import {
    addNote,
    removeNote,
    removeSavedUser,
} from '../features/user/userSlice';

export const SavedUsers = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const { savedUsers } = useSelector((state: RootState) => state.user);

    return (
        <>
            <div className="row justify-content-center">
                <div className="col-8 col-sm-4 mb-2">
                    <div
                        className="btn btn-secondary btn-sm w-100"
                        onClick={() => {
                            navigate(-1);
                        }}
                    >
                        Back
                    </div>
                </div>
            </div>
            <h1 className="text-center">Saved users</h1>
            {!savedUsers.length && (
                <h2 className="text-center">No users saved yet</h2>
            )}
            {savedUsers.map((user, idx) => (
                <div
                    key={user.id}
                    className="border mb-2 p-2 d-flex justify-content-between align-items-center position-relative"
                >
                    <div>
                        <b className="me-2">{user.name}</b>
                        <span className="text-danger">{user.age}</span>

                        <Link to={`/${user.id}`} className="stretched-link" />
                    </div>
                    <div className="d-flex flex-wrap justify-content-center ms-2 align-items-center">
                        {user.note && (
                            <span
                                className="fs-6 badge bg-light text-danger border border-danger me-2 text-wrap text-break my-1 position-relative"
                                style={{ zIndex: 1 }}
                                role="button"
                                onClick={() => {
                                    if (window.confirm('Are you sure?'))
                                        dispatch(removeNote(idx));
                                }}
                            >
                                {user.note}
                            </span>
                        )}
                        <div
                            className="btn btn-primary btn-sm me-2"
                            style={{ zIndex: 1 }}
                            onClick={() => {
                                const note = prompt('Input note');
                                if (note) dispatch(addNote({ idx, note }));
                            }}
                        >
                            {user.note ? 'Change note' : 'Add note'}
                        </div>
                        <div
                            className="btn btn-danger d-flex align-items-center"
                            style={{ zIndex: 1 }}
                            onClick={() => {
                                dispatch(removeSavedUser(user.id));
                            }}
                        >
                            <svg
                                xmlns="http://www.w3.org/2000/svg"
                                width="16"
                                height="16"
                                fill="currentColor"
                                className="bi bi-x"
                                viewBox="0 0 16 16"
                            >
                                <path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z" />
                            </svg>
                        </div>
                    </div>
                </div>
            ))}
        </>
    );
};
