import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { RootState } from '../app/store';
import { addSavedUser } from '../features/user/userSlice';

interface UserProfileProps {
    userId: string | undefined;
    className?: string;
    showTags?: boolean;
}

export const UserProfile = ({
    userId,
    showTags = false,
    className,
}: UserProfileProps) => {
    const dispatch = useDispatch();
    const { user } = useSelector((state: RootState) => state.user);
    const [showLastOnlineTime, setShowLastOnlineTime] = useState(false);
    const [displayAllTags, setDisplayAllTags] = useState(false);

    return (
        <>
            {user.name && (
                <div
                    className={
                        'd-flex justify-content-center align-items-center flex-wrap' +
                        (className ? ' ' + className : '')
                    }
                >
                    {!user.isReady && (
                        <h3 className="position-relative mb-0">
                            Loading: {userId}
                            <Link
                                to={`/${userId}`}
                                className="stretched-link"
                                replace={true}
                            />
                        </h3>
                    )}
                    {user.isReady && (
                        <>
                            <h3 className="position-relative me-2 my-1 ">
                                <b>{user.name}</b>
                                &nbsp;
                                <span className="text-danger">{user.age}</span>
                                <Link
                                    to={`/${userId}`}
                                    className="stretched-link"
                                    replace={true}
                                />
                            </h3>
                            <div
                                className="border border-warning py-1 px-2 text-warning"
                                onClick={() => {
                                    setShowLastOnlineTime(!showLastOnlineTime);
                                }}
                                role="button"
                            >
                                {showLastOnlineTime
                                    ? user.status
                                    : user.shortStatus}
                            </div>
                            {showTags && (
                                <div
                                    className={
                                        'btn btn-outline-primary btn-sm ms-2' +
                                        (user.isSaved ? ' disabled' : '')
                                    }
                                    onClick={() => {
                                        dispatch(addSavedUser(user));
                                    }}
                                >
                                    {user.isSaved ? 'Saved' : 'Save'}
                                </div>
                            )}
                            {showTags && (
                                <div className="col-12">
                                    <div
                                        className={
                                            'mt-2 text-center' +
                                            (displayAllTags
                                                ? ''
                                                : ' text-truncate') +
                                            (user.tags.length < 5
                                                ? ' text-center'
                                                : '')
                                        }
                                        onClick={() => {
                                            setDisplayAllTags(!displayAllTags);
                                        }}
                                        role="button"
                                    >
                                        {user.tags.map(tag => (
                                            <span
                                                key={tag.id}
                                                className="fs-6 badge bg-light text-dark border me-2 mb-2"
                                            >
                                                {tag.tag}
                                            </span>
                                        ))}
                                    </div>
                                </div>
                            )}
                        </>
                    )}
                </div>
            )}
        </>
    );
};
