import { getNewToken, setToken } from './../../features/auth/authSlice';
import { REFRESH_TOKEN } from './../config';
import axios from 'axios';
import { call, put, takeLeading } from 'redux-saga/effects';

const getToken = () =>
    axios.post('https://auth.api.dc2.a4sex.net/api/v1/refresh', {
        token: REFRESH_TOKEN,
    });

function* getTokenWorker(): Generator<any, any, any> {
    try {
        const res: any = yield call(getToken);
        const token: string = res.data.token;
        localStorage.setItem('token', token);
        yield put(setToken(token));
    } catch (error) {
        yield put(getNewToken());
    }
}

export function* authWatcher() {
    yield takeLeading(getNewToken.type, getTokenWorker);
}
