import { getUser } from './../../features/user/userSlice';
import { RootState } from './../store';
import {
    addChatMessages,
    addImages,
    getAllChatMessages,
    updateChatMessages,
    incGalleryCounter,
} from './../../features/chat/chatSlice';
import { IMessage, IImage } from './../../common/models/index';
import { getNewToken, setToken } from './../../features/auth/authSlice';
import axios from 'axios';
import { call, put, takeLatest, take, select } from 'redux-saga/effects';
import {
    completeLoading,
    startLoading,
} from '../../features/status/statusSlice';

interface IRequestChatMessagesArgs {
    userId: string;
    interlocutorId: string;
    offset?: number;
    token: string;
}

const requestChatMessages = ({
    userId,
    interlocutorId,
    offset,
    token,
}: IRequestChatMessagesArgs) =>
    axios.get(
        `https://dialog.api.dc2.a4sex.net/api/v1/users/${userId}/dialog`,
        {
            params: {
                tid: interlocutorId + '',
                offset,
            },
            headers: {
                Authorization: `Bearer ${token}`,
            },
        }
    );

interface IRequestImage {
    alias: string;
    fromId: number;
    token: string;
}

const requestImage = ({ alias, fromId, token }: IRequestImage) =>
    axios.get(`https://photo.api.dc2.a4sex.net/sends/${alias}`, {
        params: {
            tid: fromId,
        },
        headers: {
            Authorization: `Bearer ${token}`,
        },
    });

function* getMessages(
    data: IMessage[],
    token: string,
    lastMessageId?: number
): Generator<any, any, any> {
    const messages: IMessage[] = [];
    const images: IImage[] = [];

    for (const message of data) {
        if (lastMessageId && message.id === lastMessageId) break;
        const [prefix, alias] = message.message
            .replace(/^\[\[|\]\]$/g, '')
            .split(':');
        if (prefix !== 'IMG') {
            message.isImg = false;
            messages.push({ ...message, isImg: false });
            continue;
        }
        try {
            const res = yield call(requestImage, {
                alias,
                fromId: message.fromId,
                token,
            });

            const [photo, src]: [photo: IImage, src: string] = [
                res.data.photo,
                res.data.photo._links.origin.href,
            ];
            images.push({
                id: photo.id as string,
                src,
                alt: alias,
                sender: message.fromId,
            });
            messages.push({ ...message, isImg: true, src });
            yield put(incGalleryCounter());
        } catch (error) {
            console.log(error);
            messages.push({
                ...message,
                isImg: false,
                message: '[[IMAGE NOT FOUND]]',
            });
        }
    }
    return { messages, images, hasMessages: messages.length > 0 };
}

interface IWorkerArgs {
    type: string;
    payload: { userId: string; interlocutorId: string };
}

function* getAllMessagesWorker({
    payload,
}: IWorkerArgs): Generator<any, any, any> {
    yield put(getUser(payload.interlocutorId));
    yield put(startLoading());
    let offset = 0;
    interface IChatData {
        messages: IMessage[];
        images: IImage[];
        add: Function;
    }
    const chhatData: IChatData = {
        messages: [],
        images: [],
        add: (_data: IChatData) => {
            chhatData.messages.push(..._data.messages);
            chhatData.images.push(..._data.images);
        },
    };
    while (true) {
        try {
            const { token } = yield select((state: RootState) => state.auth);
            const { data }: { data: IMessage[] } = yield call(
                requestChatMessages,
                { ...payload, offset, token }
            );
            const processedMessages = yield call(getMessages, data, token);
            chhatData.add(processedMessages);

            if (data.length < 15) break;
            offset += 15;
        } catch (error: any) {
            if (error.response) {
                if (error.response.status === 401) {
                    yield put(getNewToken());
                    yield take(setToken.type);
                } else {
                    console.log(error.response);
                    break;
                }
            }
        }
    }

    yield put(addChatMessages(chhatData.messages.reverse()));
    yield put(addImages(chhatData.images.reverse()));
    yield put(completeLoading());
}

function* updateMessagesWorker({
    payload,
}: IWorkerArgs): Generator<any, any, any> {
    interface IChatData {
        messages: IMessage[];
        images: IImage[];
        add: Function;
    }
    const chhatData: IChatData = {
        messages: [],
        images: [],
        add: (_data: IChatData) => {
            chhatData.messages.push(..._data.messages);
            chhatData.images.push(..._data.images);
        },
    };
    try {
        const { token } = yield select((state: RootState) => state.auth);
        const { chatMessages } = yield select((state: RootState) => state.chat);
        const { data }: { data: IMessage[] } = yield call(requestChatMessages, {
            ...payload,
            token,
        });

        const processedMessages = yield call(
            getMessages,
            data,
            token,
            chatMessages[chatMessages.length - 1].id
        );
        chhatData.add(processedMessages);
        if (processedMessages.hasImages) {
            yield put(addChatMessages(chhatData.messages.reverse()));
            yield put(addImages(chhatData.images.reverse()));
        }
    } catch (error: any) {
        if (error.response) {
            if (error.response.status === 401) {
                yield put(getNewToken());
                yield take(setToken.type);
                yield put(updateChatMessages({ ...payload }));
            } else console.log(error.response);
        }
    }
}

export function* chatWatcher() {
    yield takeLatest(getAllChatMessages.type, getAllMessagesWorker);
    yield takeLatest(updateChatMessages.type, updateMessagesWorker);
}
