import { IMessage, IImage } from './../../common/models/index';
import { createSlice } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';

export interface IChatState {
    chatMessages: IMessage[];
    gallery: IImage[];
    galleryCounter: number;
    galleryFilter: number;
}

const initialState: IChatState = {
    chatMessages: [],
    gallery: [],
    galleryCounter: 0,
    galleryFilter: 0,
};

export const chatSlice = createSlice({
    name: 'chat',
    initialState,
    reducers: {
        addChatMessages: (state, action: PayloadAction<IMessage[]>) => {
            state.chatMessages.push(...action.payload);
        },
        addImages: (state, action: PayloadAction<IImage[]>) => {
            state.gallery.push(...action.payload);
        },
        incGalleryCounter: state => {
            state.galleryCounter++;
        },
        clearChatMessages: state => {
            state.chatMessages = [];
            state.gallery = [];
            state.galleryCounter = 0;
            state.galleryFilter = 0;
        },
        setFilter: (state, action: PayloadAction<number>) => {
            state.galleryFilter = action.payload;
        },
        clearFilter: state => {
            state.galleryFilter = 0;
        },
        getAllChatMessages: (
            _,
            action: PayloadAction<{ userId: string; interlocutorId: string }>
        ) => {},
        updateChatMessages: (
            _,
            action: PayloadAction<{ userId: string; interlocutorId: string }>
        ) => {},
    },
});

// Action creators are generated for each case reducer function
export const {
    addChatMessages,
    clearChatMessages,
    addImages,
    incGalleryCounter,
    setFilter,
    clearFilter,
    getAllChatMessages,
    updateChatMessages,
} = chatSlice.actions;

export const chatReducer = chatSlice.reducer;
